code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

body::-webkit-scrollbar-track {
  box-shadow: inset 0 0 6px rgba(0, 0, 0, 0.3);
  background-color: #f5f5f5;
}

body::-webkit-scrollbar {
  width: 6px;
  background-color: #f5f5f5;
}

body::-webkit-scrollbar-thumb {
  background-color: #000000;
}

.App {
  text-align: center;
  width: 100%;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

.landing-section {
  height: 100vh;
  width: 100%;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  padding: 0 10px;
}

.page-title {
  font-size: 3.5rem;
  font-weight: bold;
  margin-bottom: 1rem;
  font-family: "Mochiy Pop One", "Roboto", sans-serif;
}

.page-title-x {
  color: #4907ff;
}

.page-description {
  font-family: "Poppins", "Roboto", sans-serif;
  font-weight: bolder;
  font-size: 1.5rem;
  font-style: italic;
  width: 100%;
  margin: 0;
}

.page-descripton-x {
  color: #4907ff;
}

.projects-section {
  display: flex;
  flex-direction: row;
  justify-content: space-evenly;
  align-items: center;
  flex-wrap: wrap;
}

.failed-state,
.loading-state {
  font-family: "Poppins", "Roboto", sans-serif;
  font-size: 2.5rem;
  font-weight: bold;
  color: gray;
}

.pagination-container {
  height: 50px;
  width: 100%;
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
  margin: 10px 0;
}

.pagination-button {
  margin: 0 2px;
  padding: 5px 10px;
  border: none;
  border: 0.5px solid #4907ff;
  border-radius: 5px;
  background-color: #f5f5f5;
  color: #4907ff;
  font-family: "Poppins", "Roboto", sans-serif;
  font-weight: bold;
}

.pagination-button:hover {
  background-color: #4907ff;
  color: #f5f5f5;
  cursor: pointer;
}

.pagination-active {
  background-color: #4907ff;
  color: #f5f5f5;
}

.pagination-active:hover {
  background-color: #f5f5f5;
  color: #4907ff;
  cursor: pointer;
}

@media screen and (max-width: 1024px) {
  .page-title {
    font-size: 3.5rem;
  }

  .page-description {
    font-size: 1.7rem;
    width: 100%;
  }
}

@media screen and (max-width: 512px) {
  .page-title {
    font-size: 1.55rem;
  }

  .page-description {
    font-size: 1rem;
    width: 100%;
  }
}

.project-card {
  max-height: 1000px;
  width: 450px;
  border: 1px solid #e6e6e6;
  border-radius: 10px;
  box-shadow: 0px 0px 10px rgba(0, 0, 0, 0.5);
  margin: 20px;
}

.project-card-image {
  max-height: 600px;
  width: 100%;
  background-color: #f5f5f5;
  border-top-right-radius: 10px;
  border-top-left-radius: 10px;
}

.card-image {
  height: 100%;
  width: 100%;
  border-top-right-radius: 10px;
  border-top-left-radius: 10px;
}

.project-card-info {
  max-height: 400px;
  width: 100%;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  background-color: #f5f5f5;
  margin: 0;
  padding: 5px 10px;
  border-bottom-right-radius: 10px;
  border-bottom-left-radius: 10px;
}

.text-container {
  height: 75%;
  width: 100%;
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  align-items: center;
  margin: 0 0 15px 0;
}

.title {
  font-family: "Mochiy Pop One", "Roboto", sans-serif;
  margin: 0 0 5px 0;
  color: #4907ff;
}

.title-x {
  color: #ff07a0;
}

.description-x {
  font-family: "Poppins", "Roboto", sans-serif;
  font-size: 0.85rem;
  color: #000;
  margin: 0;
}

.description-y {
  font-family: "Poppins", "Roboto", sans-serif;
  font-size: 0.85rem;
  color: #ff07a0;
  margin: 0;
}

.link-container {
  height: 25%;
  width: 100%;
  display: flex;
  flex-direction: row;
  justify-content: space-evenly;
  align-items: flex-start;
  margin: 0 0 15px 0;
}

.link {
  text-decoration: none;
  border: 2px solid #4907ff;
  border-radius: 5px;
  margin: 0;
  padding: 5px 10px;
  color: #4907ff;
  font-family: "Poppins", "Roboto", sans-serif;
  font-weight: bold;
}

.link:hover {
  background-color: #4907ff;
  color: #f5f5f5;
}

@media screen and (max-width: 512px) {
  .project-card {
    max-height: 667px;
    width: 300px;
    margin: 10px;
  }

  .project-card-image {
    max-height: 400px;
  }

  .project-card-info {
    max-height: 267px;
  }

  .title {
    font-size: 0.8rem;
  }

  .description {
    font-size: 0.6rem;
  }

  .link {
    font-size: 0.7rem;
  }
}

